/* TipCalculator Component Styles */

.tipCalculatorContainer {
  margin-bottom: 2rem;
  padding: 1.5rem;
  background-color: #f9f9f9;
  border-radius: 8px;
  border: 1px solid #e0e0e0;
}

.calculatorForm {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.inputGroup {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.inputGroup label {
  font-weight: 600;
  color: #333;
}

.tipAmountInput {
  padding: 0.75rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1.1rem;
  width: 100%;
}

.tipAmountInput:focus {
  border-color: #006241;
  outline: none;
  box-shadow: 0 0 0 2px rgba(0, 98, 65, 0.2);
}

.calculateButton {
  background-color: #006241;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-top: 0.5rem;
  align-self: flex-start;
}

.calculateButton:hover:not(:disabled) {
  background-color: #00412b;
}

.calculateButton:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}

.errorMessage {
  color: #d62b1f;
  font-size: 0.9rem;
  margin-top: 0.5rem;
}

.totalHoursDisplay, 
.remainderCentsDisplay {
  background-color: rgba(0, 98, 65, 0.1);
  padding: 0.75rem;
  border-radius: 4px;
  font-size: 0.95rem;
}

.totalHoursDisplay span, 
.remainderCentsDisplay span {
  font-weight: 600;
  color: #006241;
}

/* Responsive styles */
@media (max-width: 768px) {
  .tipCalculatorContainer {
    padding: 1rem;
  }
  
  .tipAmountInput {
    padding: 0.6rem;
    font-size: 1rem;
  }
  
  .calculateButton {
    padding: 0.6rem 1.2rem;
    width: 100%;
  }
} 