/* ManualInput Component Styles */

.manualInputContainer {
  margin-bottom: 2rem;
}

.tableContainer {
  overflow-x: auto;
  margin-bottom: 1rem;
}

.partnersTable {
  width: 100%;
  border-collapse: collapse;
  margin: 1rem 0;
}

.partnersTable th {
  background-color: #006241; /* Starbucks green */
  color: white;
  padding: 0.75rem;
  text-align: left;
}

.partnersTable td {
  padding: 0.5rem;
  border-bottom: 1px solid #ddd;
}

.inputField {
  width: 100%;
  padding: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1rem;
}

.inputField:focus {
  border-color: #006241;
  outline: none;
  box-shadow: 0 0 0 2px rgba(0, 98, 65, 0.2);
}

.removeButton {
  background-color: #d62b1f;
  color: white;
  border: none;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  font-size: 0.75rem;
  transition: background-color 0.3s ease;
}

.removeButton:hover {
  background-color: #b32318;
}

.addButton {
  background-color: #006241;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.addButton:hover {
  background-color: #00412b;
}

/* Responsive styles */
@media (max-width: 768px) {
  .partnersTable th,
  .partnersTable td {
    padding: 0.5rem 0.25rem;
  }
  
  .inputField {
    padding: 0.4rem;
    font-size: 0.9rem;
  }
} 