/* FileUpload Component Styles */

.fileUploadContainer {
  margin-bottom: 2rem;
  text-align: center;
}

.dropZone {
  border: 2px dashed #006241; /* Starbucks green */
  border-radius: 10px;
  padding: 2rem;
  text-align: center;
  cursor: pointer;
  margin: 1rem 0;
  transition: all 0.3s ease;
  background-color: rgba(0, 98, 65, 0.05);
}

.dragActive {
  background-color: rgba(0, 98, 65, 0.1);
  border-color: #00412b;
}

.fileInput {
  display: none;
}

.dropZoneContent {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 120px;
}

.acceptedFormats {
  font-size: 0.8rem;
  color: #666;
  margin-top: 0.5rem;
}

.selectedFileInfo {
  background-color: rgba(0, 98, 65, 0.1);
  padding: 0.5rem 1rem;
  border-radius: 5px;
  margin-top: 0.5rem;
}

/* Responsive styles */
@media (max-width: 768px) {
  .dropZone {
    padding: 1rem;
  }
  
  .dropZoneContent {
    min-height: 100px;
  }
} 