/* Main App Styles */

:root {
  --primary-color: #006241; /* Starbucks green */
  --primary-dark: #00412b;
  --background-color: #f9f9f9;
  --text-color: #333;
  --error-color: #d62b1f;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--background-color);
  color: var(--text-color);
}

.App {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem;
}

.App-header {
  text-align: center;
  padding: 2rem 0;
  border-bottom: 1px solid #eaeaea;
  margin-bottom: 2rem;
}

.App-header h1 {
  margin: 0;
  font-size: 2.5rem;
  color: var(--primary-color);
}

.App-subtitle {
  margin-top: 0.5rem;
  font-size: 1.2rem;
  color: #666;
}

.App-main {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.App-section {
  margin-bottom: 1rem;
}

.App-loading {
  text-align: center;
  padding: 1rem;
  background-color: rgba(0, 98, 65, 0.1);
  border-radius: 8px;
  margin-top: 1rem;
}

.App-error {
  text-align: center;
  padding: 1rem;
  background-color: rgba(214, 43, 31, 0.1);
  border-radius: 8px;
  margin-top: 1rem;
  color: var(--error-color);
}

.App-footer {
  margin-top: 4rem;
  padding-top: 2rem;
  text-align: center;
  border-top: 1px solid #eaeaea;
  color: #666;
}

.App-footer-note {
  font-size: 0.8rem;
  margin-top: 0.5rem;
}

/* Print styles */
@media print {
  .App-header {
    padding: 1rem 0;
  }
  
  .App-footer {
    display: none;
  }
  
  .App-section {
    margin-bottom: 0;
    page-break-inside: avoid;
  }
}

/* Responsive styles */
@media (max-width: 768px) {
  .App {
    padding: 0.5rem;
  }
  
  .App-header {
    padding: 1rem 0;
  }
  
  .App-header h1 {
    font-size: 1.8rem;
  }
  
  .App-subtitle {
    font-size: 1rem;
  }
}
