/* ResultsDisplay Component Styles */

.resultsContainer {
  margin-top: 2rem;
  padding: 1.5rem;
  background-color: #f9f9f9;
  border-radius: 8px;
  border: 1px solid #e0e0e0;
}

.summaryBox {
  background-color: #006241;
  color: white;
  padding: 1rem;
  border-radius: 8px;
  margin-bottom: 1.5rem;
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
}

.summaryItem {
  display: flex;
  flex-direction: column;
}

.summaryLabel {
  font-size: 0.9rem;
  opacity: 0.9;
}

.summaryValue {
  font-size: 1.5rem;
  font-weight: 600;
}

.tableContainer {
  overflow-x: auto;
  margin-bottom: 1.5rem;
}

.resultsTable {
  width: 100%;
  border-collapse: collapse;
  margin: 1rem 0;
}

.resultsTable th {
  background-color: #006241;
  color: white;
  padding: 0.75rem;
  text-align: left;
}

.resultsTable td {
  padding: 0.75rem;
  border-bottom: 1px solid #ddd;
}

.billBreakdown {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.bill {
  background-color: rgba(0, 98, 65, 0.1);
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
  font-size: 0.9rem;
  white-space: nowrap;
}

.printSection {
  margin-top: 1.5rem;
  display: flex;
  justify-content: flex-end;
}

.printButton {
  background-color: #006241;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  transition: background-color 0.3s ease;
}

.printButton:hover {
  background-color: #00412b;
}

/* Print styles */
@media print {
  .printButton {
    display: none;
  }
  
  .resultsContainer {
    border: none;
    padding: 0;
    background-color: transparent;
  }
}

/* Responsive styles */
@media (max-width: 768px) {
  .resultsTable th,
  .resultsTable td {
    padding: 0.5rem 0.25rem;
    font-size: 0.9rem;
  }
  
  .bill {
    font-size: 0.8rem;
    padding: 0.2rem 0.4rem;
  }
  
  .summaryValue {
    font-size: 1.2rem;
  }
} 